import Vue from "vue";
import VueRouter from "vue-router";
import store from "@/store";
Vue.use(VueRouter);

const routes = [
  {
    name: "Register",
    path: "/reg",
    // 使用的是webpack提供的路由懒加载方法，只有当路由切换到对应的路径时才会去加载组件
    // 让首页加载的文件体积更小，打开更快
    component: () => import("@/views/register"),
    // 路由守卫，进入时判断是否已经登录
    beforeEnter: (to, from, next) => {
      if (store.state.userInfo.uname) {
        alert("您已登录！若要切换账号请先退出登录！");
        next("/");
      } else {
      }
      next();
    },
  },
  {
    name: "Login",
    path: "/login",
    // 使用的是webpack提供的路由懒加载方法
    component: () => import("@/views/login"),
    // 路由守卫，进入时判断是否已经登录
    beforeEnter: (to, from, next) => {
      if (store.state.userInfo.uname) {
        alert("您已登录！若要切换账号请先退出登录！");
        next("/");
      } else {
      }
      next();
    },
  },
  {
    name: "Layout",
    path: "/",
    component: () => import("@/views/layout"),
    redirect: "/home",
    children: [
      {
        name: "Home",
        path: "home",
        component: () => import("@/views/home"),
      },
      {
        name: "Food",
        path: "food",
        component: () => import("@/views/food"),
      },
      {
        name: "Shop",
        path: "shop",
        component: () => import("@/views/shop"),
      },
      {
        name: "UserInfo",
        path: "user-info",
        component: () => import("@/views/user/userInfo"),
      },
      {
        name: "UserAvatar",
        path: "user-avatar",
        component: () => import("@/views/user/useravatar"),
      },
      {
        name: "UserPwd",
        path: "user-pwd",
        component: () => import("@/views/user/userpwd"),
      },
      {
        name: "Order",
        path: "order",
        component: () => import("@/views/user/order"),
      },
      // {
      //   name: "Favorites",
      //   path: "favorites",
      //   component: () => import("@/views/user/favorites"),
      // },
      {
        name: "ShopCart",
        path: "shop-cart",
        component: () => import("@/views/user/shopcart"),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

// 建立路由白名单，让那些无需登录就可进入的页面记录
const whiteList = ["/login", "/reg"];

// 全局路由守卫
router.beforeEach((to, from, next) => {
  const token = store.state.token;
  if (token) {
    // 如果有token，表示已经登录过，还没有退出登录过
    if (!store.state.userInfo.uname) {
      // 本来可以放在登录页面发起用户请求的，
      // 但是当在浏览其中放入了token字符串，
      // 当用户再次进入系统时不会直接进入登录页面，
      // 这个时候就不会发起获取用户信息的请求，所以放在全局前置路由守卫中比较合理
      store.dispatch("initUserInfo");
    }
    if (whiteList.includes(to.path)) {
      next();
    } else {
      if (store.state.userInfo.status == 1) {
        alert("该账户已被冻结！");
        store.commit("updateToken", "");
        store.commit("updateUserInfo", {});
        next("/login");
      }
    }
    next();
  } else {
    // 如果无token
    // 判断是否是去白名单的路由，是则放行
    if (whiteList.includes(to.path)) {
      next();
    } else {
      alert("登录已过期或还未登陆，请重新登录。");
      next("/login");
    }
  }
});

export default router;
