import axios from "axios";
import store from "@/store";
// 设置网络请求订单基地址
// 手机热点
// export const baseURL = "http://192.168.202.75:8181";
// 电脑热点
// export const baseURL = "http://192.168.173.155:8181";
// 刘坚手机
// export const baseURL = "http://192.168.237.75:8181";
// 校园网
// export const baseURL = "http://10.19.249.119:8181";
// 本地地址
export const baseURL = "http://localhost:8181";

const myAxios = axios.create({
  baseURL,
});

// =====================================定义请求拦截器=====================================
myAxios.interceptors.request.use(
  function (config) {
    // 成功时调用

    // 建立请求白名单，登录和注册页面不需要携带token
    const whiteAPIList = ["api/reg", "/api/login"];
    if (!whiteAPIList.includes(config.url)) {
      // 让请求都携带统一的请求头

      config.headers.Authorization = store.state.token;
    }
    // 将config交给axios源码内，在发起请求
    return config;
  },
  function (error) {
    // 失败时发挥一个拒绝状态的Primise
    return Promise.reject(error);
  }
);

// =====================================定义响应拦截器=====================================
