// 用于定义发起网络请求的方法
// 每个方法只负责一个URL地址
import request from "@/utils/request";
import store from "@/store";

//======================================以下是获取用户信息和登录注册======================================

// 用户注册
export const registerAPI = (date) => {
  return request({
    url: "/user/regist",
    method: "POST",
    data: date,
  });
};
// 用户登录
export const loginAPI = (data) => {
  return request({
    url: "/user/login",
    method: "post",
    data,
  });
};
// 获取用户信息
export const getUserInfoAPI = () => {
  return request({
    url: "/user/info",
    headers: {
      Authorization: store.state.token,
    },
  });
};

//======================================以下是与店铺相关的接口======================================

// 获取左侧店铺咧表
export const shopListAPI = () => {
  return request({
    url: "/room/list",
  });
};
// 获取店铺页面信息、菜品列表
export const shopAPI = (shopID) => {
  return request({
    url: "/shop/list",
    method: "post",
    data: {
      sid: shopID,
    },
  });
};
// 获取菜品界面的菜品详情
export const foodDetailAPI = (foodID) => {
  return request({
    method: "POST",
    url: "/dishes",
    data: {
      foodID: foodID,
    },
  });
};
// 获取菜品的用户评论
export const foodCommentsListAPI = (data) => {
  return request({
    url: "/dishes/comment",
    method: "post",
    data: data,
  });
};
// 用户发表评论
export const commentsAPI = (data) => {
  return request({
    method: "POST",
    url: "/comment/add",
    data: data,
  });
};

