// 修改购物车菜品勾选状态
export const UpdateCheckedByIdAPI = (cartID, isChecked) => {
  return request({
    url: "/shopcar/select",
    method: "POST",
    data: {
      carid: cartID,
      status: isChecked,
    },
  });
};
