import {
  getShopCartListAPI,
  UpdateCheckedByIdAPI,
  DeleteCartByIdAPI,
  updateShopCartCountAPI,
} from "@/api";
// search的小仓库
// state:仓库存储数据的地方
const state = {
  cartList: [],
};
// mutations:修改state的唯一手段
const mutations = {
  GETCARTLIST(state, cartList) {
    state.cartList = cartList;
  },
};
// actions:处理action，可以书写自己的业务逻辑，也可以处理异步任务
const actions = {
  // 获取购物车列表
  async getCartList({ commit }) {
    const { data: res } = await getShopCartListAPI(); //数据格式不好
    if (res.code === 0) {
      commit("GETCARTLIST", res.data);
    }
  },
  // 将产品添加到购物车或对购物车菜品数量进行修改
  async updateShopCart({ commit }, { cartID, num }) {
    let { data: res } = await updateShopCartCountAPI(cartID, num);
    if (res.code == 0) {
      return "OK";
    } else {
      return Promise.reject(new Error(res.message));
    }
  },
  // 删除菜品
  async deleteCartListBySkuId({ commit }, cartID) {
    const { data: res } = await DeleteCartByIdAPI(cartID);
    if (res.code === 0) {
      return "ok";
    } else {
      return Promise.reject(new Error("failed to deleteCartListBySkuid"));
    }
  },
  // 修改商品状态
  async updateCheckedById({ commit }, { cartID, isChecked }) {
    const { data: res } = await UpdateCheckedByIdAPI(cartID, isChecked);
    if (res.code === 0) {
      return "ok";
    } else {
      return Promise.reject(new Error("failed to updateCheckedById"));
    }
  },
  //   删除所有选中的菜品
  deleteAllCheckedCart({ dispatch, getters }) {
    let PromiseAll = [];
    getters.cartList.forEach((item) => {
      let promise =
        item.status == 0 ? dispatch("deleteCartListBySkuId", item.carid) : "";
      PromiseAll.push(promise);
    });
    return Promise.all(PromiseAll);
  },
  //   删除所有下架菜品
  deleteAllForsaleCart({ dispatch, getters }) {
    let PromiseAll = [];
    getters.cartList.forEach((item) => {
      let promise =
        item.dstatus == 1 ? dispatch("deleteCartListBySkuId", item.carid) : "";
      PromiseAll.push(promise);
    });
    return Promise.all(PromiseAll);
  },
  // 全选按钮
  updateAllCartChecked({ dispatch, state }, isChecked) {
    let PromiseAll = [];
    state.cartList.forEach((item) => {
      let promise = dispatch("updateCheckedById", {
        cartID: item.carid,
        isChecked,
      });
      PromiseAll.push(promise);
    });
    return Promise.all(PromiseAll);
  },
};
// getters：处理计算属性，用于简化仓库数据，让组件获取仓库数据更方便
// 可以按将来再说组件中要用的数据进行简化
const getters = {
  cartList(state) {
    return state.cartList || [];
  },
};
export default {
  state,
  mutations,
  actions,
  getters,
};
